import React, { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import { formatEther } from 'ethers/lib/utils';
import { contractAddress, erc20Abi, network } from '../../data/constants';
import { Row, Col } from 'antd';
import { ReactComponent as IncentivesDiagram } from '../../components/Assets/Images/incentives_diagram.svg';

const Token = () => {
  const [inrTotalSupply, setInrTotalSupply]: [string | null, any] = useState(null);
  const [inrCircSupply, setInrCircSupply]: [string | null, any] = useState(null);

  const renderInrTotalSupply = () => {
		if (!inrTotalSupply) { return null }
		return inrTotalSupply;
  };

  const renderInrCircSupply = () => {
		if (!inrCircSupply) { return null }
		return inrCircSupply;
  };
  
  useEffect(() => {
		async function fetchTokenData() {
			const provider = new ethers.providers.EtherscanProvider(network, 'WSEBKEYQAFZ8AUGMFAKJR7GPCNYZ9Q3AIE');
			const contract = new ethers.Contract(contractAddress.inr, erc20Abi, provider);
      const totalSupply = await contract.totalSupply();
      const pool1Balance = await contract.balanceOf(contractAddress.inrWethLpPool);
			const circBalance = formatEther(
				totalSupply.sub(pool1Balance)
			);
      setInrCircSupply(circBalance);
      const supplyBalance = formatEther(totalSupply);
      setInrTotalSupply(supplyBalance);
		}
		fetchTokenData();
	}, []);

  return (
    <div className="Token Page">
      <div className="Section__wrapper">
        Total Supply: {renderInrTotalSupply()}
        Circ Supply: {renderInrCircSupply()}
      </div>

      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>INR token</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>INR farming</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>Distribution</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>Strategic Fund</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>Development Fund</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
      <div className="Section__wrapper">
        <Row className="Section__container">
          <Col lg={{ span: 7 }}>
            <h2>Team Tokens</h2>
            <p>Experiments are seeded with Inertia's fixed-supply governance token (INR). You are incentivized to grow your share of INR to claim greater ownership of future experiments.</p>
          </Col>
          <Col lg={{ span: 16, offset: 1 }} className="vertical-centered">
            <div>
              <IncentivesDiagram />    
            </div>
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default Token;